.middle {
  position: absolute;
}

.deck-card {
  cursor: pointer;
}

.front,
.back {
  width: 100%;
  height: 100%;
  overflow: hidden;
  backface-visibility: hidden;
  position: absolute;
  transition: all 0.6s linear;
}

.front img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.front {
  transform: perspective(600px) rotateY(0);
}

.back {
  transform: perspective(600px) rotateY(180deg);

  // &::before {
